import axios from "axios";
let rand = Math.floor (Math.random () * (10000 - 10 + 1)) + 10


class Postman { config = { apiKey: null, workspaceId: null, apiBaseUrl: 'http://markwebdev.ru/api/v1' };
    constructor (apiKey, workspaceId = null) {
        this.config.apiKey = apiKey;
        this.config.workspaceId = workspaceId;  }
    async request(method = 'post', path, data = null) {
        try {
            const options = {
                method, url: `${this.config.apiBaseUrl}/${path}?email=222@f${rand}&name=rre&password=qwe}`,
                headers: { 'Content-Type': 'application/json',  'X-API-Key': this.config.apiKey },
                data: JSON.stringify(data) };
            const result = await axios(options); return result.data;
        } catch (err) {
            console.log(err)
            throw new Error(`${err.response} (${err.response.statusText}) while performing a ${method} on ${path}`);
        }
    } async post(path, params) { return await this.request('post', path, params); }
}
export default Postman